.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f3f3f3;
  }

  .card {
    padding: 20px;
    width: 300px;
    text-align: center;
    background: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }

  .profile-icon {
    width: 60px;
    height: 60px;
    background: #007bff;
    color: white;
    font-size: 24px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin: 0 auto 10px;
  }

  .balance {
    font-size: 18px;
    font-weight: bold;
    margin: 15px 0;
  }

  .withdraw-button {
    background: #dc3545;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .withdraw-button:hover {
    background: #c82333;
  }

